                .data.mb-4
                    .data-title 
                    .data-value 3466
                .data.mb-4
                    .data-title 
                    .data-value 445445
                .data.mb-4
                    .data-title  
                    .data-value 02.02.2020
                .data.mb-4
                    .data-title  
                    .data-value 555 - 555
            .data.mb-4
                .data-title 
                .data-value         
            .data.mb-4
                .data-title  
                .data-value         
            .data-row-2
                .data.mb-4
                    .data-title 
                    .data-value 9999988888889999
                .data.mb-4
                    .data-title 
                    .data-value 786 - 35 - 987
            .data
