</template>

<script>
    import vSelect from 'vue-select';
    import 'vue-select/dist/vue-select.css';

    export default {
        name: 'component.popup.support',
        props: {
            type: {
                type: String,
                default: 'default'
            },

            // props for admin
            userActive: {
                type: Boolean,
                default: false
            }

        },
        components: {
            vSelect,
        },
        data() {
            return {
                activeSection: 'consultation',
                orders: [
                    ' 245-657  25.05.2020',
                    ' 245-658  25.05.2020',
                    ' 245-659  25.05.2020',
                    ' 245-660  25.05.2020',
                ],
                imgPopup: false,
            }
        },


        methods: {

            async close() {
                this.$emit('popupEvent', false);
                this.imgPopup = false;
            },
        }
    }
</script>